<template>
  <section class="p-1">
    <Header />
    <Navbar />
    <router-view></router-view>
    <Footer />
  </section>
  <Loading />
  <EditModal />
  <MessageModal />
</template>

<script>
import Header from '@/components/layout/HeaderModel.vue';
import Navbar from '@/components/layout/NavbarModel.vue';
import Footer from '@/components/layout/FooterModel.vue';
import Loading from '@/components/models/LoadingModel.vue';
import EditModal from '@/components/models/EditModal.vue';
import MessageModal from '@/components/models/MessageModal.vue';

export default {
  components: {
    Header,
    Navbar,
    Footer,
    Loading,
    EditModal,
    MessageModal,
  },
  created() {
    const token = document.cookie.replace(/(?:(?:^|.*;\s*)LULU-Bake\s*=\s*([^;]*).*$)|^.*$/, '$1');
    this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
  },
};
</script>
