<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <div class="notepad content-box">
    <div class="top rounded-0">
      <h3 class="text-uppercase text-center text-nowrap">shopping list</h3>
    </div>
    <div class="paper d-flex flex-column justify-content-between">
      <table class="table table-borderless">
        <thead>
          <tr>
            <th scope="col">Product</th>
            <th scope="col">Size</th>
            <th scope="col">Price</th>
            <th scope="col">Qty</th>
            <th scope="col">Sub</th>
            <th scope="col">Alt</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="(item, index) in cartList" :key="index">
            <tr>
              <td class="col-4">
                <div class="d-flex align-items-center">
                  <i
                    class="bi bi-circle-fill me-1"
                    :class="{
                      'text-danger': item.product.category === 'pie',
                      'text-info': item.product.category === 'cake',
                      'text-secondary': item.product.category === 'cookie',
                    }"
                    style="font-size: 8px"
                  ></i
                  >{{ item.product.title }}
                </div>
              </td>
              <template v-for="(data, key) in item.option" :key="key">
                <template v-if="key === 0">
                  <td class="col-2">{{ data.info.size }}</td>
                  <td class="col-2">${{ data.info.price }}</td>
                  <td class="col-2">
                    <a
                      class="page-link d-inline"
                      href="#"
                      @click.prevent="addCart(-1, data, item)"
                      :class="{ disabled: data.quantity <= 1 }"
                      >-</a
                    ><span class="luluText">{{ data.quantity }}</span
                    ><a
                      class="page-link d-inline"
                      href="#"
                      @click.prevent="addCart(1, data, item)"
                      :class="{ disabled: data.quantity === 10 }"
                      >+</a
                    >
                  </td>
                  <td class="col-2">${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
                  <td class="col-1 text-center">
                    <a href="#" @click.prevent="this.$emitter.emit('editModal', data)"
                      ><i class="bi bi-pencil"></i
                    ></a>
                  </td>
                </template>
              </template>
            </tr>
            <template v-for="(data, key) in item.option" :key="key">
              <tr class="luluText" v-if="item.option.length !== 1 && key !== 0">
                <td colspan="1"></td>
                <td>{{ data.info.size }}</td>
                <td>${{ data.info.price }}</td>
                <td>
                  <a
                    class="page-link d-inline"
                    href="#"
                    @click.prevent="addCart(-1, data, item)"
                    :class="{ disabled: data.quantity <= 1 }"
                    >-</a
                  ><span class="luluText">{{ data.quantity }}</span
                  ><a
                    class="page-link d-inline"
                    href="#"
                    @click.prevent="addCart(1, data, item)"
                    :class="{ disabled: data.quantity == 10 }"
                    >+</a
                  >
                </td>
                <td>${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
                <td class="col-1 text-center">
                  <a href="#" @click.prevent="this.$emitter.emit('editModal', data)"
                    ><i class="bi bi-pencil"></i
                  ></a>
                </td>
              </tr>
            </template>
            <br />
          </template>
        </tbody>
      </table>
      <div class="modal-footer pb-0">
        <div class="mb-2 d-flex justify-content-between w-100">
          <div class="d-flex">
            <div class="d-flex align-items-center me-4">
              <i class="bi bi-circle-fill text-danger me-1" style="font-size: 8px"></i>
              <h6>pie</h6>
            </div>
            <div class="d-flex align-items-center me-4">
              <i class="bi bi-circle-fill text-info me-1" style="font-size: 8px"></i>
              <h6>cake</h6>
            </div>
            <div class="d-flex align-items-center">
              <i class="bi bi-circle-fill text-secondary me-1" style="font-size: 8px"></i>
              <h6>cookie</h6>
            </div>
          </div>
          <div>
            Total：$ <span class="luluText">{{ this.$toCurrency(total) }}</span>
          </div>
        </div>
        <div class="d-flex justify-content-between w-100">
          <router-link
            to="/shop"
            class="font-monospace fw-bold titleText position-relative"
            @click="this.$emitter.emit('hideModal')"
          >
            <span>Shopping</span><i class="bi bi-caret-left-fill"></i>
          </router-link>
          <router-link
            to="/shop/checkout"
            class="font-monospace fw-bold titleText position-relative"
            @click="this.$emitter.emit('hideModal')"
            ><i class="bi bi-caret-right-fill" ref="checkout"></i>Checkout
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import '@/assets/stylesheets/helpers/_label.scss';
.table > :not(caption) > * > * {
  background-color: transparent;
  padding: 0;
}
</style>

<script>
import { gsap } from 'gsap';
import { apiGetCarts, apiPostCart } from '@/assets/javascript/api';

export default {
  data() {
    return {
      cartList: [],
      cartModal: '',
      total: 0,
    };
  },
  methods: {
    addCart(num, option, item) {
      // eslint-disable-next-line no-underscore-dangle
      const data = { productId: item.product._id, option: { size: option.size, quantity: num } };
      apiPostCart(data)
        .then(() => {
          this.$emitter.emit('getCarts');
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getCarts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetCarts()
        .then((res) => {
          this.cartList = res.data.data.carts;
          // 資料要填寫一下，我後端技術不好ＱＱ
          this.populateCarts(res.data.data.carts);
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    populateCarts(data) {
      // 先將選項拉出，比對product裡頭的選項，將正確得填入選項
      data.forEach((item) => {
        item.option.forEach((e) => {
          item.product.options.forEach((index) => {
            // eslint-disable-next-line no-underscore-dangle
            if (index._id === e.size) {
              e.info = index;
              e.product = item;
            }
          });
        });
      });
      //   計算總金額
      this.total = 0;
      this.cartList.forEach((item) => {
        item.option.forEach((option) => {
          this.total += option.info.price * option.quantity;
        });
      });
    },
  },
  mounted() {
    this.getCarts();
    this.$emitter.on('getCarts', () => {
      this.getCarts();
    });
    const timeline = gsap.timeline();
    timeline.to(
      this.$refs.checkout,
      {
        opacity: 0,
        duration: 1.5,
        repeat: -1,
      },
      '<',
    );
  },
};
</script>
