<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
    ref="editModal"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content rounded-0 border-primary border-3 cartBG">
        <div class="modal-header border-primary border-3">
          <h1 class="modal-title fs-5" id="staticBackdropLabel">{{ tempProduct.title }}</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="card mb-3 border-0 cartBG">
            <div class="row g-0">
              <div class="col-md-6">
                <img :src="tempProduct.image" class="img-fluid rounded-start" alt="..." />
              </div>
              <div class="col-md-6">
                <div class="card-body">
                  <!-- size -->
                  <div class="mb-3">
                    <h5 class="text-uppercase">SIZE：</h5>
                    <template v-for="(item, index) in tempProduct.options" :key="index">
                      <input
                        type="radio"
                        class="btn-check me-md-3"
                        name="options-size"
                        :id="`size0${index}`"
                        autocomplete="off"
                        :value="item._id"
                        v-model="tempOption.size"
                        @click="tempOption.price = item.price"
                      />
                      <label class="btn border-0 position-relative me-3" :for="`size0${index}`"
                        >{{ item.size }}
                        <CircleThree :class="{ 'd-none': item._id !== tempOption.size }" />
                      </label>
                    </template>
                  </div>
                  <!-- price -->
                  <div class="mb-3">
                    <h5>PRICE：</h5>
                    <p class="ms-3">
                      $ <span class="text-secondary">{{ tempOption.price }}</span> / EACH
                    </p>
                  </div>
                  <!-- 數量＆＄ -->
                  <div class="mb-3">
                    <h5 class="text-uppercase">quantity：</h5>
                    <nav aria-label="..." class="me-4">
                      <ul class="pagination">
                        <li class="page-item">
                          <a
                            class="page-link rounded-0"
                            href="#"
                            @click.prevent="tempOption.quantity--"
                            :class="{ disabled: tempOption.quantity <= 1 }"
                            ><i class="bi bi-dash"></i
                          ></a>
                        </li>
                        <li class="page-item">
                          <input
                            type="text"
                            class="page-link"
                            style="width: 2.9em"
                            v-model="tempOption.quantity"
                            disabled
                          />
                        </li>
                        <li class="page-item">
                          <a
                            class="page-link rounded-0"
                            href="#"
                            @click.prevent="tempOption.quantity++"
                            :class="{ disabled: tempOption.quantity >= 10 }"
                            ><i class="bi bi-plus-lg"></i
                          ></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                  <!-- 總金額 -->
                  <div class="">
                    <h5 class="text-uppercase">total：</h5>
                    <div class="fst-italic ms-3 h4 text-secondary luluText">
                      ${{ this.$toCurrency(tempOption.price * tempOption.quantity) }}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer border-primary border-3 d-flex justify-content-between">
          <button type="button" class="btn btn-outline-danger rounded-0" @click="deletCart">
            Delete
          </button>
          <button type="button" class="btn btn-outline-primary rounded-0" @click="patchCart">
            Update
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.circle-3 {
  width: 5rem;
}
</style>

<script>
import { Modal } from 'bootstrap';
import { apiDeleteCart, apiPatchCart } from '@/assets/javascript/api';
import CircleThree from '@/components/models/circle/CircleThree.vue';

export default {
  data() {
    return {
      modal: '',
      tempProduct: {},
      tempOption: {},
    };
  },
  components: {
    CircleThree,
  },
  methods: {
    deletCart() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-underscore-dangle
      const data = { productId: this.tempProduct._id, optionId: this.tempOption._id };
      apiDeleteCart({ data })
        .then((res) => {
          console.log(res);
          this.$emitter.emit('loadingStatus', false);
          this.$emitter.emit('getCarts');
          this.closeModal();
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    patchCart() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-underscore-dangle
      const data = { productId: this.tempProduct._id, option: this.tempOption };
      apiPatchCart(data)
        .then((res) => {
          console.log(res);
          this.$emitter.emit('getCarts');
          this.$emitter.emit('loadingStatus', false);
          this.closeModal();
        })
        .catch((err) => {
          console.elog(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    openModal() {
      this.modal.show();
    },
    closeModal() {
      this.modal.hide();
      this.tempProduct = {};
      this.tempOption = {};
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.editModal);
    this.$emitter.on('editModal', (e) => {
      this.tempProduct = { ...e.product.product };
      // eslint-disable-next-line no-underscore-dangle
      this.tempOption._id = e._id;
      this.tempOption.size = e.size;
      this.tempOption.quantity = e.quantity;
      this.tempOption.price = e.info.price;
      this.openModal();
    });
  },
};
</script>
